.main{
  padding: 0 0 2em;
  &__add{
    height: 3.125em;
    width: 100%;
  }
  &__articles{
    margin-top: 3.5em;
    @include grid100gap(1.5625em);
  }
}

.article-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__link{
    color: $primary;
    text-decoration: underline;
    font-size: 1.125em;
    line-height: 150%;
  }
  &__delete{
    margin-left: 1em;
    flex-shrink: 0;
    width: 2.25em;
    height: 2.25em;
    border-radius: .4375em;
    background: #ffe8ee;
    &:hover{
      background: darken(#ffe8ee,50%);
      path{
        fill: #fff;
      }
    }
    svg{
      width: 1em;
    }
  }
}

.new-article{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__back{
    font-size: 1.125em;
    line-height: 150%;
    color: $primary;
    &:hover{
      opacity: .8;
    }
  }
  &__confirm{
    width: 2.25em;
    height: 2.25em;
    border-radius: .4375em;
  }
}

.article-form{
  margin-top: 2.625em;
  @include grid100gap(.625em);
  .fg--text{
    margin-top: 4.375em;
  }
}

.article{
  padding: 3.5em 1.125em 1.25em;
  position: relative;
  &__title,
  &__link{
    font-size: 1.125em;
    line-height: 150%;
  }
  &__title{
    font-weight: 600;
    margin-bottom: 2em;
  }
  &__link{
    text-decoration: underline;
    color: $primary;
    word-break: break-word;
  }
}

.text{
  font-size: 1.125em;
  line-height: 150%;
  margin-top: 5.5em;
  p{
    line-height: inherit;
    @include lastM(0 0 1.5em);
  }
  span{
    transition: all .3s;
    &:hover{
      cursor: pointer;
      background: #c2dfff;
    }
  }
}

.result-modal{
  position: fixed;
  left: 50%;
  transform: translate(-50%,calc(-100% - 1em));
  top: 50%;
  padding: 1.25em;
  max-width: calc(100vw - 1em);
  background: #fff;
  box-shadow: 0 .25em 1.25em rgba(0, 0, 0, 0.1);
  width: 26.5em;
  border-radius: .5625em;
  opacity: 0;
  pointer-events: none;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__title,
  &__desc,
  &__desc2,
  &__add{
    font-size: 1.125em;
    line-height: 133%;
  }
  &__title{
    color: #a5a5a5;
    b{
      color: $black;
    }
  }
  &__desc{
    margin-top: 1.125em;
    &._gray{
      color: #a5a5a5;
    }
  }
  &__bottom{
    margin-top: 1.3125em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__desc2{
    color: #a5a5a5;
  }
  &__add{
    margin-left: auto;
    font-weight: 500;
    color: $primary;
    &:hover{
      opacity: .8;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__articles{
      margin-top: 2em;
      gap: 1em;
    }
  }

  .article-item{
    &__link{
      font-size: 1em;
    }
  }

  .article-form{
    margin-top: 1.5em;
    .fg--text{
      margin-top: 1em;
    }
  }

  .article{
    padding: 2em 0 1em;
    &__link{
      font-size: .875em;
    }
    &__title{
      font-size: 1em;
      margin-bottom: 1.25em;
    }
  }

  .text{
    font-size: .875em;
    margin-top: 3em;
  }
}
