.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 32.875em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2em 2.1875em;
        border-radius: .6875em;
        display: inline-block;
    }
}

.modal-form{
    margin-top: 1.875em;
    @include grid100gap(.625em);
    .fg--text{
        textarea{
            height: 6em;
        }
    }
    &__nav{
        margin-top: .875em;
        margin-left: auto;
        display: inline-flex;
        justify-content: flex-end;
    }
    &__btn{
        height: 3em;
        padding: 0 2.3125em;
        border-radius: .5em;
        background-color: $primary;
        color: #fff;
        &:hover{
            background-color: darken($primary,10%);
            color: #fff;
        }
        span{
            font-size: 1.125em;
        }
    }
}



