.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border-radius: 0.5em;
    border: 1px solid #dee2e6;
    padding: 0 1.11em;
    height: 3em;
    width: 100%;
    font-size: 1.125em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #dee2e6;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding-top: .7em;
    padding-bottom: .7em;
    height: 17.2em;
    resize: vertical;
  }
}



