@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    border-radius: .4375em;
    &:focus {
        box-shadow: none;
    }
    svg{
        width: 1em;
    }
}

.m-btn-blue{
    background: #e4ecff;
    &:hover{
        background: darken(#e4ecff,10%);
    }
}

.m-btn-green{
    background: #f0f9e4;
    &:hover{
        background: darken(#f0f9e4,10%);
    }
}
